@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
}
h2 {
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 300;
  text-align: center;
}

.btn {
  margin-top: 4vh;
  padding: 1vh 4vw;
  min-width: 20vw;
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 400;
}
.btn.wide {
  min-width: 30vw;
}

input {
  margin-top: 1em;
}

.App {
  width: 100%;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.width-100 {
  width: 100vw;
}
.width-90 {
  width: 90vw;
}
.width-80 {
  width: 80vw;
}
.width-70 {
  width: 70vw;
}
.width-60 {
  width: 60vw;
}
.width-50 {
  width: 50vw;
}
.width-40 {
  width: 40vw;
}
.width-30 {
  width: 30vw;
}

.align-center {
  text-align: center;
}

.login-link-container {
/*  margin-top: 0.5em;*/
}

.landing-container {
  text-align: center;
}

.get-started {
  margin-top: 4vh;
  padding: 1vh 10vh;
  font-family: Nunito, Helvetica, Arial, sans-serif;
  font-weight: 400;
}

